header {
    margin-top: 5rem;
    height: calc(100vh - 5rem);
    display: grid;
    place-items: center;
    background: black;
    transition: var(--transition);
}

h3 {
    display: inline-block;
    color: transparent;
    margin: 0;
    background-clip: text;
    background-image: url('../../assets/Unstable.gif');
    background-position: center;
}

.header__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.header__profile {
    width: 24rem;
    height: 22rem;
    border-radius: 20%;
    margin-bottom: 1.5rem;
    overflow: hidden;
}

.header__container p > span {
    text-align: center;
    font-size: 1.5rem;
    display: inline-block;
    color: rgba(255, 55, 45, .9);
    width: 15rem;
    white-space: nowrap;
    overflow: hidden;
    animation: typing 2s,
    cursor .4s step-end infinite;
    margin: 0.6rem 0 2rem;
}

span:before {
    content: '';
    animation: animate infinite 8s;
}

@keyframes animate {
    0%{
        content: 'Hello friend..';
    }

    10%{
        content: 'Привет, друг'
    }

    20%{
        content: 'Hola amigo';
    }

    30%{
        content: 'こんにちは、友達'
    }

    40%{
        content: 'Hallo Freund';
    }

    50%{
        content: 'Hej kompis';
    }

    60%{
        content: 'हैलो दोस्त';
    }

    70%{
        content: 'Olá amigo';
    }

    80%{
        content: 'Halló vinur';
    }

    90%{
        content: 'Bonjour mon ami';
    }

    100%{
        content: 'سلام رفیق';
    }

}

.header__cta {
    display: flex;
    align-items: center;
    gap: 1.2rem;
}

.header__socials {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    font-size: 2rem;
}

.header__socials a {
    width: 3rem;
    height: 3rem;
    aspect-ratio: 1/1;
    color: #666;
    transition: .5s;
    display: grid;
    place-items: center;
    border-radius: 50%;
    overflow: hidden;

}

.header__socials a::before {
    background: #5cff10;
    transition: .5s;
    transform: scale(.9);
    z-index: -1;
}

.header__socials a:hover::before {
    transform: scale(1.1);
    box-shadow: 0 0 15px #5cff10;
}

.header__socials a:hover {
    color: #5cff10;
    box-shadow: 0 0 10px #5cff10;
    text-shadow: 0 0 10px #5cff10;
    transform: translate(-0.5rem);
}

@media screen and (max-width: 1024px) {
    header {
        margin-top: 0;
        height: 100vh;
    }
}

@media screen and (max-width: 600px) {
    .header__profile {
        width: 14rem;
        height: 17rem;
        max-width: 60%;
        max-height: 35vh;
    }
    
    .main {
        height: 15rem;
    }

    .header__container p {
        width: 100%;
        text-align: center;
    }

    .header__cta {
        gap: 1rem;
    }

    .header__socials {
        display: none;
    }
}


@keyframes cursor {
    50% {border-color: transparent;}
}

@keyframes typing {
    from {width: 0}

}